import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";

import { fetchFilters, activeFilterChanged, selectAll } from "./filtersSlice";
import store from "../../store";

import Spinner from "../spinner/Spinner";

var classNames = require("classnames");

const HeroesFilters = () => {
  const filters = selectAll(store.getState());
  const { loadingStatus: filtersLoadingStatus, activeFilter } = useSelector(
    (state) => state.filters
  );
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(fetchFilters());
    // eslint-disable-next-line no-use-before-define
  }, []);

  if (filtersLoadingStatus === "fetching") {
    return <Spinner />;
  } else if (filtersLoadingStatus === "fetch-error") {
    return (
      <h5 className="text-center mt-5">
        Ошибка во время попытки загрузки фильтров
      </h5>
    );
  }

  return (
    <div className="card shadow-lg mt-4">
      <div className="card-body">
        <p className="card-text">Отфильтруйте героев по элементам</p>
        <div className="btn-group">
          {filters.map((item) => {
            let btnClass = null;
            switch (item.name) {
              case "all":
                btnClass = "btn-outline-dark";
                break;
              case "fire":
                btnClass = "btn-danger";
                break;
              case "water":
                btnClass = "btn-primary";
                break;
              case "wind":
                btnClass = "btn-success";
                break;
              case "earth":
                btnClass = "btn-secondary";
                break;
              default:
                btnClass = null;
            }
            return (
              <button
                key={item.id}
                className={classNames("btn", btnClass, {
                  active: item.name === activeFilter,
                })}
                onClick={() => dispatch(activeFilterChanged(item.name))}
              >
                {item.label}
              </button>
            );
          })}
        </div>
      </div>
    </div>
  );
};

export default HeroesFilters;
